import { _decorator, BoxCollider2D, Collider2D, Component, Contact2DType, IPhysics2DContact, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Eliminator')
export class Eliminator extends Component {
    start() {
        
        let p = this.node.getComponent(Collider2D);
        /*
        this.area= MaskSplitter.calculateEnvolArea(p.points);
        console.log("El obj "+ this.node.name+" tiene el area "+this.area)*/
 
        p.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);

    }
    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
      
       // if (otherCollider.node.name == "Stomper") {
       // console.log("collision")
       otherCollider.node.destroy();
 

    }

    update(deltaTime: number) {
        
    }
}


